<template>
	<view class="register">
		<view class="formBox">
			<u-cell-group class="tsbox">
				<u-cell-item title="上报类型" :value="fytitle(details.type)" :arrow="false"></u-cell-item>
				<u-cell-item title="举报内容" :value="fycon(details.typeReport)" :arrow="false"></u-cell-item>
				<u-cell-item title="举报地" :value="fydd(details.address)" :arrow="false"></u-cell-item>
				<u-cell-item title="单位地点" :value="details.addressDetailedInformation" :arrow="false"></u-cell-item>
				<u-cell-item title="异常行为描述" :value="details.describe" :arrow="false"></u-cell-item>
				<u-cell-item title="相关嫌疑人" :value="details.relatedSuspects" :arrow="false"></u-cell-item>
			</u-cell-group>
		</view>
		<view class="formBox" style="margin-top: 20rpx;">
			<view class="mititle">
				<image style="width: 26rpx;margin-right: 12rpx;" mode="widthFix" src="/static/img/timi.png"></image>
				附件
			</view>
			<u-row gutter="20">
				<u-col span="4" v-for="(item,index) in details.imglist" :key="index">
					<u-image width="100%" height="160rpx" :src="item" class="imgBox"></u-image>
					</u-col>
					</u-row>
			
		</view>
	</view>
</template>

<script>
	import {
		getObj
	} from '../../api/report.js'
	import {
		getDicts,
	} from '../../api/index.js'
	export default {
		data() {
			return {
				details: {
					imglist:[]
				},
				zdlist: [],
				tylist: [],
				degree: [],
				active:[],
			}
		},
		onLoad() {
			getDicts("sb_lx").then(res => {
				if (res.code == 200) {
					this.zdlist = res.data;
				}
			})
			getDicts("report_area").then(res => {
				if (res.code == 200) {
					this.ddlist = res.data;
				}
			})
			getObj(this.$route.query.id).then(res => {
				if (res.code == 200) {
					this.details = res.data;
					this.details.imglist = res.data.sfFj.split(',');
					if(this.details.type==0){
						getDicts("sb_lx_peopel").then(res => {
							if (res.code == 200) {
								this.tylist = res.data;
							}
						})
					}else{
						getDicts("sb_lx_goods").then(res => {
							if (res.code == 200) {
								this.tylist = res.data;
							}
						})
					}
				}
			})
		},
		methods: {
			fytitle(name) {
				let result = null;
				this.zdlist.map((item) => {
					if (item.dictValue == name) {
						result = item.dictLabel;
					}
				})
				return result;
			},
			fycon(name) {
				let result = null;
				this.tylist.map((item) => {
					if (item.dictValue == name) {
						result = item.dictLabel;
					}
				})
				return result;
			},
			fydd(name) {
				let result = null;
				this.ddlist.map((item) => {
					if (item.dictValue == name) {
						result = item.dictLabel;
					}
				})
				return result;
			},
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
	}

	.mybox {
		text-align: center;
		margin: 50rpx 0;
	}

	.formBox {
		padding: 0 30rpx 30rpx 30rpx;
		background-color: #fff;
	}
	.formBox .imgBox{
		margin-top: 20rpx;
	}

	.mititle {
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		height: 90rpx;
		line-height: 90rpx;
		margin: 0 -30rpx;
		padding: 0 30rpx;
		border-bottom: 1px solid #f1f1f1;
	}
    .tsbox /deep/ .u-cell{
		padding: 26rpx 0;
	}
	.tsbox /deep/ .u-cell_title {
		color: #777777;
	}

	.tsbox /deep/ .u-cell__value {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
	}
</style>